.navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 13px 0;
  padding-bottom: 10px;
}

.navigation__toggle {
  display: none;
  border: none;
  background-color: #ffffff;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-image: url("../img/nav-opened.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.navigation__toggle--open {
  background-image: url("../img/nav-closed.svg");
}
.navigation__toggle--js {
  display: block;
}

@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }

  .navigation {
    padding: 24px 0;
  }
}
@media (min-width: 1440px) {
  .navigation {
    padding: 54px 0;
  }
}
.logo:hover {
  opacity: 0.6;
}
.logo:active {
  opacity: 0.3;
}

.logo__image {
  width: 190px;
  height: 38px;
}

@media (min-width: 768px) {
  .logo__image {
    width: 174px;
    height: 50px;
  }
}
@media (min-width: 1440px) {
  .logo__image {
    width: 202px;
    height: 59px;
  }
}
.navigation-list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
  text-align: center;
  width: 100%;
}

.navigation-list--js {
  position: absolute;
  top: 63px;
  width: 100vw;
  min-width: 320px;
  left: 50%;
  transform: translate(-50%);
  display: none;
}

.navigation-list--open {
  display: block;
}

.navigation-list__item {
  border-top: 1px solid #e6e6e6;
}

.navigation-list__link {
  text-decoration: none;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  padding: 22px;
  display: block;
}

@media (min-width: 768px) {
  .navigation-list {
    position: static;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 446px;
    background-color: transparent;
    transform: none;
  }

  .navigation-list__item {
    border-top: none;
  }

  .navigation-list__link {
    font-size: 18px;
    line-height: 24px;
    padding: 15px 5px;
    position: relative;
  }
  .navigation-list__link:not(.navigation-list__link--activ):hover {
    opacity: 0.6;
  }
  .navigation-list__link:not(.navigation-list__link--activ):active {
    opacity: 0.3;
  }

  .navigation-list__link--activ-index::after {
    display: block;
    content: "";
    height: 2px;
    background-color: #68b738;
    position: absolute;
    bottom: 6px;
    left: 5px;
    right: 5px;
  }

  .navigation-list__link--activ::after {
    display: block;
    content: "";
    height: 2px;
    background-color: #68b738;
    position: absolute;
    bottom: 6px;
    left: 5px;
    right: 5px;
  }
}
@media (min-width: 1440px) {
  .navigation-list {
    width: 480px;
    font-size: 20px;
    line-height: 20px;
  }

  .navigation-list__link {
    font-size: 20px;
    padding: 15px 2px;
  }

  .navigation-list__link--index {
    color: #ffffff;
  }

  .navigation-list__link--activ-index::after {
    background-color: #ffffff;
    display: block;
    content: "";
    height: 2px;
    position: absolute;
    bottom: 6px;
    left: 5px;
    right: 5px;
  }
}
